/* purgecss start ignore */

.lc-input-group {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: flex-start;
  margin: -12px;
  margin-bottom: 12px;
}

.lc-input-group .lc-input-wrapper, .lc-input-group .lc-button {
    margin: 12px;
  }

.lc-input-group .lc-input-wrapper, .lc-input-group .lc-input-select {
    flex: 1 1 0%;
  }

.lc-input-group .lc-input {
    margin: 0;
  }

.lc-input-group .lc-button {
    flex: none;
  }

